.popup-login {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.body {
  background-color: #4f2ee8;
}

.popup-body.login {
  width: 580px;
  text-align: center;
  margin: auto;
  padding: 80px 15px 50px;
  position: relative;
}

@media screen and (max-width: 767px) {
  .popup-body.login {
    width: 335px;
    padding: 50px 20px 40px;
  }
}

.popup-close {
  cursor: pointer;
  font-size: 28px;
  position: absolute;
  top: 24px;
  right: 24px;
}

@media screen and (max-width: 767px) {
  .popup-close {
    font-size: 20px;
    top: 10px;
    right: 10px;
  }
}

.popup-body {
  background-color: #f3f3f3;
  border: 2px solid #111;
  border-radius: 18px;
}

@media screen and (max-width: 767px) {
  .popup-body {
    width: 335px;
    padding: 50px 20px 40px;
  }
}

.input {
  width: 500px;
  border: 2px solid #111;
  border-radius: 40px;
  margin: 0 auto 30px;
  padding: 25px;
}

@media screen and (max-width: 767px) {
  .input {
    width: 295px;
    padding: 16px;
  }
}

.button-signup {
  width: 500px;
  color: #f3f3f3;
  cursor: pointer;
  background-color: #111;
  border-radius: 40px;
  margin: 20px auto 30px;
  padding: 20px;
  font-size: 22px;
}

@media screen and (max-width: 767px) {
  .button-signup {
    width: 320px;
    margin: 15px auto;
    padding: 16px;
    font-size: 18px;
  }
}

.svg-icon {
  fill: #111;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

@media screen and (max-width: 767px) {
  .svg-icon {
    width: 20px;
    height: 20px;
  }
}

.lebel {
  margin-top: 4px;
  position: relative;
}

::-ms-input-placeholder {
  color: #111;
  font-size: 20px;
  font-weight: 500;
}

::placeholder {
  color: #111;
  font-size: 20px;
  font-weight: 500;
}

@media screen and (max-width: 767px) {
  ::-ms-input-placeholder {
    font-size: 16px;
  }

  ::placeholder {
